@import 'var';

$prime: #ff6e48;
$second: #1b2431;

body, html {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-size: 10px;
    background-color: $second; }

.page {
    font-family: 'Roboto', sans-serif;
    width: 100%;
    display: grid;
    grid-template-rows: 7rem 80vh 20rem;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas: "header header header header header" "inhalt inhalt . . ." "zitat zitat zitat zitat zitat";
    header {
        grid-area: header;
        background-color: $prime;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 175%;


        h1 {
            display: inline-block;
            width: auto;
            padding: 0;
            margin: 0 0 0 3rem;
            color: #fff; }
        nav {
            font-size: 1.6rem;
            margin: 0 3rem 0 0;
            menu {
                menuitem {
                    a {
                        color: #fff;
                        text-decoration: none;
                        margin: 0 0 0 1rem; } } } } }

    .inhalt {
        grid-area: inhalt;
        justify-content: center;
        .intro {
            margin: 10rem 0 0 85%;
            h2 {
                font-size: 2rem;
                color: #fff;
                display: flex; }

            p {
                font-size: 1.6rem;
                color: #fff; } } }

    .zitat {
        grid-area: zitat;
        background-color: $prime;
        display: flex;
        justify-content: center;
        align-items: center;
        .zitat-inhalt {
            margin: 0;
            padding: 0;
            font-size: 2.2rem;
            color: #fff;
            text-align: center;
            width: 80%;
            display: inline-block; } } }


.g-ytsubscribe {
    position: relative;
    left: 5%; }


.number-box {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 0 0 2rem 0;

    .numbers {
        color: white;
        line-height: 1;
        position: relative;
        display: flex;
        overflow: hidden;
        align-items: center;
        height: 5rem;
        font-size: 5rem;
        span {
            z-index: 1;
            display: flex;
            flex-direction: column;
            height: 100%;
            transition: all 2s ease;
            transform: translateY(0);
            line-height: 1;
            span {
                flex: 0 0 100%;
                height: 100%; } } }

    .subtitle {
        color: white;
        font-size: 1.2rem;
        display: flex;
        width: 100%;
        margin: 0 0 2rem 0;
        flex-direction: row;
        justify-content: space-between;
        .diff {} } }


.glitch {
    color: white;
    font-size: 10rem;
    width: 40rem;
    position: relative;
    font-family: 'Roboto';
    left: 5%;

    &::after {
        content: attr(data-trick);
        position: absolute;
        top: 0;
        left: 0.5rem;
        color: white;
        background-color: $prime;
        overflow: hidden;
        text-shadow: -0.5rem 0 $prime;
        clip: rect(0, 90rem, 0, 0);
        animation: noise 2s infinite linear alternate-reverse; }

    &::before {
        content: attr(data-trick);
        position: absolute;
        top: 0;
        left: -1rem;
        color: white;
        background-color: $prime;
        overflow: hidden;
        text-shadow: 0.3rem 0 $prime;
        clip: rect(0, 90rem, 0, 0);
        animation: noise 4s infinite linear alternate-reverse; } }

@keyframes noise {
    $steps: 25;
    @for $i from 0 through $steps {
        #{ percentage($i * (1/$steps))} {
            clip: rect(random(12)+rem, 99.99rem, random(12)+rem, 0); } } }
